<template>
  <div class="container about">
    <h1>新規登録</h1>
    <hr />
    <form @submit.prevent="submit">
      <div class="form-group row">
        <label for="staticEmail" class="col-sm-4 col-form-label">
          終端アドレス
        </label>
        <div class="col-sm-8">
          <input
            type="text"
            class="form-control"
            v-model="userIpV6"
            placeholder="2001:db8::"
            :class="{ 'is-invalid': isSubmitted && $v.userIpV6.$error }"
          />
          <div
            v-if="isSubmitted && !$v.userIpV6.required"
            class="invalid-feedback"
          >
            終端アドレスは必須です
          </div>
          <div
            v-if="isSubmitted && !$v.userIpV6.isNGNAddr"
            class="invalid-feedback"
          >
            NGN網内アドレスのみ指定可能です
          </div>
        </div>
      </div>
      <div class="form-group row">
        <label for="tunnelProtocol" class="col-sm-4 col-form-label">
          希望接続種別
        </label>
        <div class="col-sm-8">
          <select
            class="custom-select"
            id="tunnelProtocol"
            v-model="tunnelProtocol"
            :class="{ 'is-invalid': isSubmitted && $v.tunnelProtocol.$error }"
          >
            <option selected value="">選択してください</option>
            <option value="EtherIP">Ether/IP</option>
            <option value="GREIP">GRE/IP</option>
            <option value="IPIP">IP/IP</option>
          </select>
          <div
            v-if="isSubmitted && !$v.userIpV6.required"
            class="invalid-feedback"
          >
            希望接続種別は必須です
          </div>
        </div>
      </div>
      <div class="form-group row">
        <label class="col-sm-4 col-form-label"> 広報アドレスの種別 </label>
        <div class="col-sm-8">
          <div class="form-check form-check-inline">
            <input
              class="form-check-input"
              type="checkbox"
              id="addressCategoryClosed"
              v-model="addressCategory"
              value="closed"
            />
            <label class="form-check-label" for="addressCategoryClosed">
              網内限定アドレス
            </label>
          </div>
          <div class="form-check form-check-inline">
            <input
              class="form-check-input"
              type="checkbox"
              id="addressCategoryIPoE"
              v-model="addressCategory"
              value="IPoE"
            />
            <label class="form-check-label" for="addressCategoryIPoE">
              IPoEアドレス（VNE契約有）
            </label>
          </div>
          <div
            v-if="isSubmitted && !$v.addressCategory.checkAddrCat"
            class="invalid-feedback"
          >
            広報アドレスの種別は、一種類以上選択してください
          </div>
        </div>
      </div>
      <div class="form-group row">
        <label for="dynamicRoutingProtocol" class="col-sm-4 col-form-label">
          ルート広報プロトコル
        </label>
        <div class="col-sm-8">
          <select
            class="custom-select"
            id="dynamicRoutingProtocol"
            v-model="dynamicRoutingProtocol"
            :class="{
              'is-invalid': isSubmitted && $v.dynamicRoutingProtocol.$error,
            }"
          >
            <option selected value="">選択してください</option>
            <option value="BGP">BGP</option>
            <option value="OSPF">OSPF</option>
            <option value="RIP">RIP</option>
          </select>
          <div
            v-if="isSubmitted && !$v.userIpV6.required"
            class="invalid-feedback"
          >
            ルート広報プロトコルは必須です
          </div>
        </div>
      </div>
      <h2>利用規約</h2>
      <div class="form-group"><ToS></ToS></div>
      <div class="form-group form-check">
        <input
          type="checkbox"
          class="form-check-input"
          id="exampleCheck1"
          v-model="agree"
          :class="{
            'is-invalid': isSubmitted && $v.agree.$error,
          }"
          @change="$v.agree.$touch()"
        />
        <label class="form-check-label" for="exampleCheck1">
          利用規約に同意します
        </label>
        <div
          v-if="isSubmitted && !$v.userIpV6.required"
