<script>
import ToS from "@/components/ToS.vue";
import { required } from "vuelidate/lib/validators";
const ipaddr = require("ipaddr.js");
const isNGNAddr = (value) => {
  const rangeList = [
    // 東 2021/02/10
    [ipaddr.parse("2404:01a8:7e00::"), 40],
    [ipaddr.parse("2404:01a8::"), 32],
    [ipaddr.parse("2001:0c90::"), 33],
    [ipaddr.parse("2408:0210::"), 30],
    [ipaddr.parse("2400:2410::"), 30],
    [ipaddr.parse("2409:0010::"), 30],
    [ipaddr.parse("240b:0010::"), 30],
    [ipaddr.parse("2404:7a80::"), 30],
    [ipaddr.parse("2405:6580::"), 30],
    [ipaddr.parse("2400:4050::"), 30],
    [ipaddr.parse("2401:4d40::"), 30],
    [ipaddr.parse("2001:0f70::"), 30],
    [ipaddr.parse("240b:c0c4::"), 30],
    // 西 2021/02/10
    [ipaddr.parse("2001:a7ff:5e00::"), 40],
    [ipaddr.parse("2001:a7ff::"), 32],
    [ipaddr.parse("2001:a000::"), 24],
    [ipaddr.parse("2001:a200::"), 23],
    [ipaddr.parse("2400:2650::"), 30],
    [ipaddr.parse("2409:0250::"), 30],
    [ipaddr.parse("240b:0250::"), 30],
    [ipaddr.parse("2404:7a84::"), 30],
    [ipaddr.parse("2405:6584::"), 30],
    [ipaddr.parse("2400:4150::"), 30],
    [ipaddr.parse("2400:4150::"), 30],
    [ipaddr.parse("2401:4d44::"), 30],
    [ipaddr.parse("2001:0f74::"), 30],
    [ipaddr.parse("240b:c0cc::"), 30],
  ];
  try {
    const inputAddr = ipaddr.parse(value);
    return ipaddr.subnetMatch(inputAddr, rangeList, "unknown") !== "unknown";
  } catch (error) {
    return false;
  }
};
const checkAddrCat = (value) => {
  return value.length >= 1;
};
export default {
  data() {
    return {
      userIpV6: "",
      tunnelProtocol: "",
      dynamicRoutingProtocol: "",
      addressCategory: [],
      isSubmitted: false,
      agree: false,
    };
  },
  validations: {
    userIpV6: {
      required,
      isNGNAddr,
    },
    tunnelProtocol: {
      required,
    },
    dynamicRoutingProtocol: {
      required,
    },
    addressCategory: {
      checkAddrCat,
    },
    agree: {
      required: { allowFalse: false },
    },
  },
  methods: {
    submit: function (e) {
      this.isSubmitted = true;
      this.$v.$touch();
      console.log(this.userIpV6);
      console.log(this.tunnelProtocol);
      console.log(this.dynamicRoutingProtocol);
      console.log(this.addressCategory);
      console.log(this.agree);
      if (this.$v.$invalid) {
        // エラーの場合
      } else {
        // ここで送信
        e.preventDefault();
      }
    },
  },
  components: {
    ToS,
  },
};
</script>